a {
  position: relative;
  z-index: 1;
}
a:hover {
  z-index: 2;
}
a:hover .rect {
  display: block;
}
a:hover .st6 {
  fill: #ff8800;
}

g {
  transition: all 0.25s ease;
}
g .rect {
  display: none;
}

#Seattle {
  cursor: pointer;
  font-size: 1.25rem;
  animation: 1s fadeIn;
  animation-fill-mode: forwards;
  visibility: hidden;
  animation-delay: 0s;
}
#Seattle:hover {
  fill: white;
  filter: drop-shadow(3px 5px 2px rgba(0, 0, 0, 0.4));
}
#Seattle:hover .rect {
  display: block;
}

